___CSS_LOADER_EXPORT___.push([module.id, `/* Navbar container */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #40576e;
  border-bottom: 1px solid #e9ecef;
  position: sticky;
  top: 0;
  z-index: 100;
}

/* Brand/Home section */
.nav-brand .nav-link {
  font-size: 1.5rem;
  font-weight: bold;
  color: #faefef;
  text-decoration: none;
}

.nav-brand .nav-link:hover {
  color: #007bff;
}

/* Links section */
.nav-links {
  display: flex;
  gap: 1rem;
}

.nav-links .nav-link {
  color: #ebe7e7;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  transition: background-color 0.2s, color 0.2s;
}

.nav-links .nav-link:hover {
  background-color: #e9ecef;
  color: #007bff;
}
