___CSS_LOADER_EXPORT___.push([module.id, `/* TrainDesert.css */
.train-desert {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
}

.train-desert header h1 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 30px;
  color: #2c3e50;
}

.train-desert h2 {
  font-size: 1.5rem;
  margin-top: 25px;
  margin-bottom: 15px;
  color: #34495e;
}

.train-desert p {
  margin-bottom: 15px;
}

.train-desert ul {
  padding-left: 20px;
  margin-bottom: 15px;
}

.train-desert li {
  margin-bottom: 10px;
}

.train-desert img {
  display: block;
  margin: 20px auto;
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.train-desert a {
  color: #2980b9;
  text-decoration: none;
}

.train-desert a:hover {
  text-decoration: underline;
}
